<template>
    <div>
        <h2> 个人信息</h2>
        <el-form>
            <div class="container">
                <el-form-item label="姓名">
                    <el-input v-model="profile.name"></el-input>
                </el-form-item>
                <el-form-item label="城市">
                    <el-input v-model="profile.citry"></el-input>
                </el-form-item>
                <el-form-item label="出生年月">
                    <el-input v-model="profile.birth"></el-input>
                </el-form-item>
            </div>
        </el-form>
    </div>
</template>


<script>
export default {
    props: ['profile']
}

</script>




